import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getClassOwners = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  ownerTypeId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassOwner',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      ownerTypeId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const saveClassOwner = ({ classId, teacherId, ownerTypeId } = {}) =>
  request({
    prefix,
    url: '/ClassOwner',
    method: 'POST',
    params: {},
    data: { classId, teacherId, ownerTypeId },
  });

export const exportClassOwners = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
} = {}) =>
  requestDownload({
    prefix,
    defaultFileName: 'Exported File.xlsx',
    url: '/ClassOwner/ExportClassOwners',
    method: 'POST',
    params: { trainTypeId, facultyId, courseId, studyFieldId, classId },
    data: {},
  });



