import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form } from 'antd';
import { TableDataEditRow, SelectAjax, CartHeadAction } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { BtnExport } from 'components/common/button';
import { getClassOwners, saveClassOwner, exportClassOwners } from 'services/classOwner';
import { getStaffs } from 'services/staff';
import { equalUndefinedNull } from 'utils/functionUtil';
import FormSearchHomeroomTeacher from './FormSearchHomeroomTeacher';

const columns = [
  {
    title: 'Tên lớp',
    dataIndex: 'classLabel',
    key: 'classLabel',
  },
  {
    title: 'Số SV',
    dataIndex: 'noOfStudent',
    key: 'noOfStudent',
  },
  {
    title: 'Niên khóa',
    dataIndex: 'courseYear',
    key: 'courseYear',
  },
  {
    title: 'Khóa học',
    dataIndex: 'courseLabel',
    key: 'courseLabel',
  },
  {
    title: 'Tên khoa',
    dataIndex: 'facultyLabel',
    key: 'facultyLabel',
  },
  {
    title: 'Chuyên ngành',
    dataIndex: 'studyFieldLabel',
    key: 'studyFieldLabel',
  },
  {
    title: 'Chủ nhiệm lớp',
    dataIndex: 'teacherName',
    key: 'teacherName',
    editable: true,
    renderFormItemEdit: ({ form, record }) => (
      <Form.Item>
        {form.getFieldDecorator('teacherId', {
          initialValue: record.teacherId,
        })(
          <SelectAjax
            keyValue="staffId"
            keyLabel="fullname"
            form={form}
            service={getStaffs}
            allowGetObjSelected
          />
        )}
      </Form.Item>
    ),
  },
  {
    title: 'Cố vấn học tập ',
    dataIndex: 'advisorName',
    key: 'advisorName',
    editable: true,
    renderFormItemEdit: ({ form, record }) => (
      <Form.Item>
        {form.getFieldDecorator('advisorId', {
          initialValue: record.advisorId,
        })(
          <SelectAjax
            keyValue="staffId"
            keyLabel="fullname"
            form={form}
            service={getStaffs}
            allowGetObjSelected
          />
        )}
      </Form.Item>
    ),
  },
];

class ListHomeroomTeacherPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };
  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleSaveRowData = async (rowData, oldRowData) => {
    const { objSelected, classId, teacherId, advisorId } = rowData;
    const { teacherId: oldTeacherId, advisorId: oldAdvisorId } = oldRowData;
    const dataTeacherReq = {
      classId,
      teacherId,
      ownerTypeId: 1,
    };
    const dataAdvisorReq = {
      classId,
      teacherId: advisorId,
      ownerTypeId: 2,
    };
    try {
      if (!equalUndefinedNull(teacherId, oldTeacherId) && teacherId !== oldTeacherId) {
        await saveClassOwner(dataTeacherReq);
      }
      if (!equalUndefinedNull(advisorId, oldAdvisorId) && advisorId !== oldAdvisorId) {
        await saveClassOwner(dataAdvisorReq);
      }
      return {
        ...rowData,
        teacherName: objSelected.teacherId.fullname,
        advisorName: objSelected.advisorId.fullname,
      };
    } catch (e) {
      return null;
    }
  };

  handleExport = async () => {
    await exportClassOwners(getQueryParams());
  };

  render() {
    return (
      <Card title="Giáo viên chủ nhiệm">
        <CartHeadAction>
          <BtnExport onClick={this.handleExport} />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchHomeroomTeacher />
          </Col>
        </Row>
        <Row>
          <Row>
            <Col span={24}>
              <TableDataEditRow
                reload={this.state.reload}
                paramSearch={this.state.paramSearch}
                service={getClassOwners}
                columns={columns}
                handleSaveRowData={this.handleSaveRowData}
                rowKey="classId"
              />
            </Col>
          </Row>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListHomeroomTeacherPage />
  </Page>
);



